nav.MainMenu {
    background-color: rgba(0,0,0,0.8);
}
.MainMenu ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    align-items: center;
    width: 80%;
    text-align: center;
    margin: 0 auto;
    justify-content: space-between;
}
.MainMenu ul li{
    color: #d0cfce;
    transition: 0.2s;
    padding: 10px 0;
}
.MainMenu ul li:hover{
    color: #ffffff;
}
